import React, { useState, useEffect, Fragment } from 'react';
import { Container, Form, Row, Col, Button } from 'react-bootstrap';
import axios from 'axios';

const CommentsPage = (props, { isAuthenticated }) => {


    let [comments, setComments] = useState([])
    const [text, setText] = useState("")
    // const [replyComment, setReplyComment] = useState("")


    useEffect(() => {
        getComments()
    }, [props.post_id])

    let getComments = async () => {
        let response = await fetch(`http://localhost:8000/posts/${props.post_id}/comments`)
        let data = await response.json()
        setComments(data)
    }

    const createComment = async () => {
        let formField = new FormData()

        formField.append("text", text)
        // formField.append("reply_comment", replyComment)

        await axios({
            method: "post",
            url: `http://localhost:8000/posts/${props.post_id}/comments/create/`,
            data: formField
        }).then((response) => {
            console.log(response.data);
            getComments()
        })
    }

    function getCommentContent(comment, index) {

        if (comment.reply_comment === null) {
            return (<div style={{ paddingTop: "10px", paddingBottom: "10px" }}>

                <Row style={{ paddingTop: "20px" }}>
                    <Col style={{ "background": "#FFFFFF", "paddingTop": "20px", "borderRadius": "20px" }}>
                        <p key={index} style={{ color: "black" }}>{comment.text}</p>
                    </Col>
                </Row>
            </div>)
        }
    }

    const guestLinks = () => (
        <Fragment>
            <p2>To comment, please login</p2>
        </Fragment>
    )

    const authLinks = () => (
        <Form>
            <Form.Group className="mb-3">
                <Form.Control
                    as="textarea"
                    style={{ height: '150px' }}
                    value={text}
                    onChange={(e) =>
                        setText(e.target.value)
                    }

                    placeholder="Text"
                />
            </Form.Group>

            <Form.Group className="mb-3">
                <Button
                    onClick={createComment}
                    variant="danger"
                    style={{ width: "100%" }}
                >
                    COMMENT
                </Button>
            </Form.Group>
        </Form>
    )

    return (
        <Container style={{ paddingTop: "40px" }}>

            {isAuthenticated ? authLinks() : guestLinks()}

            <div style={{ "paddingTop": "10px" }}>
                {comments
                    .slice(0)
                    .reverse()
                    .map((comment, index) => (
                        getCommentContent(comment, index)
                    ))}

            </div>



        </Container>
    )

}


export default CommentsPage

